.headerTitle {
    display: flex;
    align-items: center;
    flex-direction: column;
    font-family: "Montserrat", sans-serif;
    line-height: 1.2;
    letter-spacing: 2px;
    font-weight: 400;
    font-size: 25px;
    text-transform: capitalize;
    margin-bottom: 60px;
}

.title {
    text-align: center;
}

.line {
    height: 1px;
    width: 55px;
    margin: 30px 0;
    background-color: darkgrey;
}